@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
  width: 50%;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;

}
.game__wrapper{
  position: absolute;
  width: 100%;
}
@media only screen and (max-width: 600px) {
  .wrapper{
    width: 90%;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
  
  }
}
.card--open{
    
   
    background-color: rgb(7, 224, 98);
    margin: 10px 0;
    padding-top: 100%;
    position: relative;
    border-radius: 8px;
}

.card{
    position: absolute;
    width: 100%;
    border-radius: 8px;
    left: 0;
    top: 0;
}
.button--wrapper__start{
    background-color: rgba(47, 79, 79, 0.9);
    position: absolute;
    right: 0px;
    left: 0px;
    top:0px; 
    bottom:0px;
    z-index: 2;
    text-align: center;
    display: flex;
    justify-content: center;
  

}
.title{
    font-family: 'Dancing Script', cursive;
    margin: 0 0 10px 0;
    font-size: 64px;
    color: rgb(214, 214, 214);
}
.paragraph{
    color: rgb(182, 182, 182);
    margin: 0 0 20px 0;
}
.button__start{
    background-color: lightsalmon;
    border: none;
    font-size: 16px;
    padding: 10px 20px;
    color: rgb(41, 41, 41);
    cursor: pointer;
   
}
.button__start:hover{
    opacity: 0.7;
    transition: 0.5s;
}
.button--content{
    margin-top: 15%;
}
.button__restart{
    margin: 10px 0;
    border-radius: 5px;
    
}
